$black: #2a2a24;

html,
body,
#root {
    margin: 0;
    padding: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    overflow: hidden;
    font-size: 14px;
    font-family: "News Cycle", sans-serif;
    background: #f1f4f8;
    -webkit-font-smoothing: antialiased;
  }

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
  
#root {
  overflow: auto;
}

canvas {
    position: absolute;
}

ul {
  position: fixed;
  bottom: 0;
  right: 0;
  top: 0;
  display: flex;
  margin: 12px;
  flex-direction: column;
  list-style: none;
  justify-content: flex-end;
}

li {
  width: 400px;
  height: 200px;
  background: #ffffff;
  margin: 10px;
  border-radius: 16px;
  stroke: transparent;
}

iframe {
  width: 400px;
  height: 200px;
  border-radius: 16px;
}

.nortMessage {
  width: 400px;
  height: 40px;
  border-radius: 16px;
  background: #d89cb0;
  margin: 10px;
  font-size: 23px;
  font-family: monospace;
  text-align: center;
  position: relative;
}

.battenNotif {
  text-align: center;
  position: center;
  bottom: 0;
  right: 0;
  top: 0;
  display: inline-block;
  vertical-align: center;
  padding: 12px;
}